import React from "react";
import { ThemeProvider } from "@material-ui/styles";
import "./styles/styles.scss";

import {
  CssBaseline,
  Button,
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Grid,
  Paper,
  makeStyles
} from "@material-ui/core";

import {
  BrowserRouter as Router,
  HashRouter,
  Switch,
  Route,
  Link,
  useRouteMatch,
  useParams
} from "react-router-dom";

import { createThemeA, createThemeB, ThemeContext } from "./theme";

import Home from "./Home";
import Portfolio from "./Portfolio";
import About from "./About";
import { PAGES } from './constants'

export default function App() {
  const [isD, setIsD] = React.useState(true);
  const theme = isD ? createThemeA() : createThemeB();

  const [page, setPage] = React.useState(PAGES.home) 
   
  return (
    <ThemeContext.Provider value={{isD, setIsD}}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <HashRouter basename={process.env.PUBLIC_URL}>
          <Switch>
            <Route path="/about">
              <About setPage={setPage}/>
            </Route>
            <Route path="/portfolio">
              <Portfolio setPage={setPage}/>
            </Route>
            <Route path="/">
              <Home setPage={setPage} />
            </Route>

          </Switch>
        </HashRouter>
        {/* {page === PAGES.home && <Home setPage={setPage} />} */}
        
        {/* {page === PAGES.about && <About setPage={setPage} /> } */}
        {/* {page === PAGES.portfolio && <Portfolio setPage={setPage} /> } */}
        {/* <WorkSecurity /> */}
      </ThemeProvider>
    </ThemeContext.Provider>
  );
}
