import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Switch } from 'react-router-dom';
import App from "./App";

const rootElement = document.getElementById("root");
ReactDOM.render(
  <BrowserRouter>
    <Switch>
      <React.StrictMode>
        <App />
      </React.StrictMode>
    </Switch>
  </BrowserRouter>,
  rootElement
);
