import React, { useEffect, useRef, useState } from "react";

import {
  CssBaseline,
  Button,
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  makeStyles,
} from "@material-ui/core";
import { WbSunny, Brightness3 } from "@material-ui/icons";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useRouteMatch,
  useParams
} from "react-router-dom";

import { ThemeContext } from './theme'
import { PAGES } from './constants'

 var prevScrollpos = window.pageYOffset
let showNav, hideNav
let a
window.onscroll = function() {
  var currentScrollPos = window.pageYOffset;
  if (window.pageYOffset <= 400){
    showNav && showNav()
  }
  else if (prevScrollpos > currentScrollPos) {
    // document.getElementsByClassName("header").style.top = "0";
    clearTimeout(a)
    a = setTimeout(() => {
      showNav && showNav()
    }, 400)
    
  } else {
    // document.getElementsByClassName("header").style.top = "-50px";
    clearTimeout(a)
    a = setTimeout(() => {
      hideNav && hideNav()
    }, 400)
  }
  prevScrollpos = currentScrollPos;
}

const Header = ({setPage, classes}) => {
    const {isD, setIsD} = React.useContext(ThemeContext)

    const iconType = isD ? <WbSunny /> : <Brightness3 />;
    const handleClickDark = () => {
        setIsD(s => !s);
    };

    const headerEl = useRef(null);

    const [top, setTop] = useState(0)
    useEffect(() => {
      // showNav = () => headerEl.current.style.marginTop = "0"
      // hideNav = () => headerEl.current.style.marginTop = "-50px"
      showNav = () => { 
        setTop(0)
      }
      hideNav = () => {
        setTop(-50)
      }
    }, [])

    const useStyles = makeStyles((theme) => ({
      logo:{
        textDecoration: "none",
        fontWeight: 600,
        fontSize: "1.2rem",
      },
    }));
    const headerCss = useStyles();

    return <header style={{top:top}} className={classes.header}>
    <Link to="/"  color="secondary" className={`${headerCss.logo} ${classes.logo}`} variant="h5">Niya</Link>
    <div className={classes.headerButton}>
    <Button href="#text-buttons" color="secondary" className={classes.portfolioButton} onClick={() => setPage(PAGES.home)} component={Link} to="/">Portfolio</Button>
    <Button href="#text-buttons" color="secondary" className={classes.portfolioButton} onClick={() => setPage(PAGES.about)} component={Link} to="/about" >About</Button>
    {/* <Button href="#text-buttons" color="secondary" className={classes.portfolioButton} onClick={() => setPage(PAGES.portfolio)}>Portfolio</Button> */}
    <Button href="#text-buttons" color="secondary" className={classes.portfolioButton} onClick={() => setPage('')}>Contact</Button>
    <IconButton
        aria-label="account of current user"
        aria-controls="menu-appbar"
        aria-haspopup="true"
        onClick={handleClickDark}
        color="secondary"
        size="small"
        className={classes.headerIcon}
      >
        {iconType}
      </IconButton>
    </div>
    
  </header>
  }

  export default Header