const ScrollArrow = () =>{

    const [showScroll, setShowScroll] = useState(false)
  
    const checkScrollTop = () => {
      if (!showScroll && window.pageYOffset > 400){
        setShowScroll(true)
      } else if (showScroll && window.pageYOffset <= 400){
        setShowScroll(false)
      }
    };
  
    const scrollTop = () =>{
      window.scrollTo({top: 0, behavior: 'smooth'});
    };
  
    window.addEventListener('scroll', checkScrollTop)
  
    return (
        <Fab size="small" aria-label="go top" className={"scrollTop"} onClick={scrollTop} style={{position: "fixed", right: "1rem", bottom: "1rem", height: 40, display: showScroll ? 'flex' : 'none'}}>
            <KeyboardArrowUp />
        </Fab>
    );
  }
  



